

import React from "react"

const Blogposts = ({props}) => {
  console.log('props:', props)
  return(
    <ul>
      {props.map(each => {
        return (
        <li key={each.id}>
          <h3>{each.title}</h3>
          <p>by <span>{each.author}</span>. Likes: {each.likes} </p>
        </li>)
      })}
    </ul>
  )

}

export default Blogposts