import React, {useState, useEffect} from "react"

import blogService from "./services/blogs.js"


import Blogposts from "./components/Blogposts"
import Form from "./components/Form"


const { getAll, create } = blogService


function App() {
  const [data, setData] = useState([])
  const [inputData, setInputData] = useState({title: '' })


  useEffect(()=> {
    getAll().then(res=> {
      console.log(res)
      setData(res)
    })
  }, [])



  const submitHandler = (e) => {
    e.preventDefault()
    console.log("submitHandler: ", inputData)

    create(inputData)
    .then(res=> {
      console.log("post res: ", res)
      setInputData({title: '' })
      console.log('input cleared!')
      setData([...data, res])
    })

  }

  const inputHandler = (e) => {
    const newPost = {
      title: e.target.value,
      author: "Mike",
      likes: 0
    }
    console.log("newPost: ", newPost)
    setInputData(newPost)
  }


  return (
    <center className="App">
      <header className="App-header">
        <Blogposts props={data} />
      </header>
      <Form inputHandler={inputHandler} inputData={inputData} submitHandler={submitHandler} value={inputData.title } />

    </center>
  );
}

export default App;
