


const Form = ({ inputHandler, inputData, submitHandler, value }) => {
  console.log("form: ", inputData)
  console.log("title: ", inputData.title)
  return(
    <form name="form" onSubmit={submitHandler}>
      <input type="text" required value={inputData.title} placeholder="Add new Post" onChange={inputHandler} />
      <button type="submit" >Submit</button>
    </form>
  )
}

export default Form